// Web fonts
@mixin font-face($family, $file) {
  @font-face {
    font-family: $family;
    src        : url("#{$path-font}/#{$file}-webfont.eot");
    src        : url("#{$path-font}/#{$file}-webfont.eot?#iefix") format('embedded-opentype'), url("#{$path-font}/#{$file}-webfont.woff") format('woff'), url("#{$path-font}/#{$file}-webfont.ttf") format('truetype');
  }
}

// Text

@mixin text($size, $l-height: null, $l-spacing: null, $weight: null, $color: null, $family: null) {
  font-size     : $size;
  line-height   : $l-height;
  font-weight   : $weight;
  color         : $color;
  letter-spacing: $l-spacing;
  font-family   : $family;
}

//RGBA
@mixin alpha-color($color, $opacity: 0.3) {
  background: $color; /* The Fallback */
  background: rgba($color, $opacity);
}

// Center Text in Div
@mixin text-center() {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

// Shine Effect
@mixin fade-transition($element) {
	-webkit-transition: $element 0.15s ease-in-out;
	-moz-transition: $element 0.15s ease-in-out;
	-ms-transition: $element 0.15s ease-in-out;
	-o-transition: $element 0.15s ease-in-out;
	transition: $element 0.15s ease-in-out;
}